.links {
 background-color: transparent;
  border:none;
  color: white;  
  cursor: pointer;
  color: white;
  text-decoration:none;
  display: inline-block;
  padding: 10px;

}
.linkNews {
    color: white; 
      cursor: pointer;
  text-decoration:none;
  color: white;
  display: inline-block;
  margin-top: 2px;
}
.linkNews:hover {
 text-decoration:none;
 color: white;
  color: white;
}
.linkNews:after {
  content: '';
  display: block;
  height: 1px;
  width: 0;
  color: white;
  background: white;
  -webkit-transition: width .5s ease, background-color .5s ease;
  transition: width .5s ease, background-color .5s ease;
}

.linkNews:hover:after {
  width: 100%;
background: white;
color: white;
}
