.linksDrop {
   background-color: transparent;
  border:none;
  color: black;  
  cursor: pointer;
  text-decoration:none;
  color: white;
  display: inline-block;
  padding: 7px;
}
.linksDrop:active {
  text-decoration:none;
  color: white;
}

.distinct {
  color: white; 
}
.links:after {
  content: '';
  display: block;
  height: 1px;
  color: white;
  width: 0;
  background: white;
  -webkit-transition: width .5s ease, background-color .5s ease;
  transition: width .5s ease, background-color .5s ease;
}

.links:hover:after {
  width: 100%;
background: white;
color: white;
}
